@font-face {
  font-family: "rajdhani";
  src:
    url("../fonts/rajdhani-regular.woff2") format("woff2"),
    url("../fonts/rajdhani-regular.woff") format("woff");
  font-weight: 400;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "rajdhani";
