    url("../fonts/rajdhani-medium.woff2") format("woff2"),
    url("../fonts/rajdhani-medium.woff") format("woff");
  font-weight: 500;
  font-style: normal;
  font-display: swap;
}

@font-face {
  font-family: "rajdhani";
  src:
    url("../fonts/rajdhani-bold.woff2") format("woff2"),
    url("../fonts/rajdhani-bold.woff") format("woff");
  font-weight: 700;
  font-style: normal;
  font-display: swap;
}
